.input {
    width: 100%;
    height: 40px;
    padding: 0 20px;
    box-sizing: border-box;
    border: none;
    border-radius: 14px;
    resize: none;
    font-size: 20px;
    background: #e7e7e7;
    color: #464646;
}

.feeContainer {
    width: 130px;
    display: flex;
    align-content: center;
    font-size: 34px;
    color: #19547b;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

input::placeholder {
    color: #787878;
    font-size: 14px;
}
.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 500px;
    height: 100px;
    background: #fff;
    padding: 2rem;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.feeRow{
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
    height: 60px;
}

.feeData p{
    margin: 1rem 0rem 1rem 0rem;
}

.amountLegend {
    font-size: 12px;
    margin: 0;
    opacity: 1;
    color: #787878;
    border-radius: 0 0 14px 14px;
    padding: 4px 10px;
    width: fit-content;
    -webkit-transition: -webkit-transform 0.6s,opacity 0.6s;
}

.hidden {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: -webkit-transform 0.6s,opacity 0.6s;
}


.feeData p{
    font-size: 20px;
}

.inputContainer {
    width: 150px;
    height: 75px;
}

body {
    background: #ffd89b;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #19547b, #ffd89b);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #19547b, #ffd89b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
    height: -webkit-fill-available;
}

.select-style {
    border: 1px solid #ccc;
    width: 80px;
    border-radius: 3px;
    overflow: hidden;
    background: #fafafa;
}

.select-style select {
    padding: 5px 8px;
    width: 130%;
    border: none;
    box-shadow: none;
    background: transparent;
    background-image: none;
    -webkit-appearance: none;
}

.select-style select:focus {
    outline: none;
}

.currencySelectorContainer {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}